import wButton from '@/components/watch-login/watch-button.vue' //button

// 导入注册方法
import { login } from '@/network/login'

export default {
	components:{
		wButton
	},
	data() {
		return {
			windowHeight:0,//屏幕高度
			phone:'',//手机号
			password:'',//密码
			isRotate: false, //是否加载旋转
			modalName:'',//忘记密码
		}
	},
	onLoad(option) {
		if(!!Object.keys(option).length){
			this.phone = option.phone
			
		}
		
		var that = this
		uni.getSystemInfo({
		    success: function (res) {
				that.windowHeight = res.windowHeight
		        console.log('屏幕高度为'+res.windowHeight);
		    }
		});
		
	},
	computed:{
		
	},
	methods: {
		//注册
		forgetClick(){
			uni.navigateTo({
				url:'register'
			})
		},
		//登录
		startLogin(){
			var that = this
			// 防抖动
			if(that.isRotate) return
			if(!(/^1[3456789]\d{9}$/.test(that.phone))){
				uni.showToast({
					title:'手机号格式有误',
					icon:'none'
				})
				return
			}
			console.log(that.password.length)
			if(that.password.length < 6){
				uni.showToast({
					title:'密码最少为6位',
					icon:'none'
				})
				return
			}
			that.isRotate = true
			setTimeout(function() {that.isRotate = false}, 5000);
			let data = {
				phone:that.phone,
				pwd:that.password
			}
			console.log(this.$store.state.userInfo)

			login(data).then(res => {
				if(res.data.code == 400){
					uni.showToast({
						title:res.data.msg,
						icon:'none'
					})
					this.phone = ''
					this.password = ''
				}else{
					const token = res.data.data.token
					uni.setStorage({
						key:"token",
						data:token,
					})
					this.$store.commit('login',token)
					uni.showToast({
						title:'登录成功',
						icon:'none'
					})
					const pages  = getCurrentPages()
					if(pages.length == 1){
						uni.switchTab({
							url:"../Home/home"
						})
					}else{
						uni.navigateBack()
					}
				}
			})
		},
		//忘记密码
		showModal(){
			uni.navigateTo({
				url:'forget'
			})
		},
	}
}
